.no-padding{
    padding: 0;
}

.box img{
    width: 100%;
    height: 150px;
    object-fit: cover;
    margin-bottom: 10px;
}

.tag{
    cursor: pointer;
}

.tag:hover{
    box-shadow: 0px 2px 10px grey;
    transition: all 100ms ease;
}

.border-bot{
    border-bottom: 1px solid #ddd;
    padding-bottom: 5px;
}

.width-full{
    width: 100%;
}

.no-pointer{
    cursor: auto;
}
.no-pointer:focus{
    outline: none;
}

.grid-four{
    display: grid !important;
    grid-template-columns: repeat(4, 1fr);
}

.over-hidden{
    height: 80px;
    overflow: hidden;
}

.addpost{
    width: 600px;
    margin: 100px auto 0 auto;
}

.is-fixed{
    position: fixed;
    width: 100%;
    z-index: 9;
    top: 0;
}

.container-top{
    margin-top: 30px;
}   


.isHidden{
    display: none;
}